#mainPage {
  height: 100%;
  width: 100%;
}

#root {
  font-family: "Courier New", Courier, monospace;
}

#userArea {
  background-color: #aaa;
}

#output p {
  text-align: center;

  padding: 3px 0px;
  margin: 0% 0px 5px 0px;
  color: #555;
}
#meassge1 {
  width: 12rem;
}

#buttonCall {
  border-radius: 30px 0px 0px 30px;
  height: 100%;
  background-image: url("/send.png");
  background-repeat: no-repeat;
  width: 50px;
}
#handle {
  text-align: center;
  text-transform: capitalize;
  font-family: "Arial";
  text-shadow: 0px 0px #555;
}
.error {
  color: red;
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  font-weight: bolder;
  font-size: small;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  letter-spacing: 1px;
  transform: translate3d(0, 0, 0);
}

.modal-dialog {
  z-index: 11111;
}

.accordion {
  width: 53px;
}
.imgLeg {
  height: 2rem;
  width: 2rem;
  cursor: pointer;
  margin: 0 10px;
}
.imageAddFri:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}

.toast-body {
  white-space: pre-line;
}
.textLeft {
  text-align: left;
}
.textRight {
  text-align: right;
}
/* login Component */
.containerLogin {
  width: 100%;
  height: 100%;
  margin: 5px;
  text-align: center;
}
/* both */
.carousel-control-prev-icon {
  color: black;
}
#changeModeRL {
  margin-top: 10%;
}
.L-R-Leabel {
  animation-name: CardTextShow;
  animation-duration: 0.5s;
}
.containerRegisterLogin {
  margin: 5px;
  padding: 5px;
  width: 80%;
  height: 80%;
}
/* register component */

#CWE {
  background-color: #e5ebee50;
}
button {
  border-radius: 100px;
  color: white;
  border: 0;
  background-color: #575ed8;
  box-sizing: border-box;
}
button:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

body {
  font-family: "Nunito";
}

h2 {
  font-size: 18px;
  padding: 10px 20px;
  color: #575ed8;
}
label {
  box-sizing: border-box;
  display: block;
  padding: 10px 20px;
}

/* video class */
#videosCon {
  position: absolute;
  z-index: 22;
}

#oncall {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 30em;
  height: 18em;
  margin-top: -9em; /*set to a negative number 1/2 of your height*/
  margin-left: -15em; /*set to a negative number 1/2 of your width*/
  border: 1px solid #ccc;
  background-color: #f3f3f3;
}
@keyframes CardTextShow {
  0% {
    position: relative;
    top: -100px;
    opacity: 0;
    left: -100px;
  }

  100% {
    position: relative;
    top: 0px;
    opacity: 0.2;
    left: 0px;
  }
}
@keyframes errorTextShow {
  0% {
    opacity: 0;
    font-size: x-large;
  }

  100% {
    opacity: 1;
    font-size: small;
  }
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}
